body .containerOfAll{
    display: flex;
    justify-content: space-between;
    height: 100%;
    min-height: 100vh;
  
    .leftPart{
      

        transition: 0.2s;
        background-color: #fa3701;
        width: 25%;
        padding: 40px 0;
        position: relative;
        .delButtonAB{
            position: absolute;
            top: 15px;
            right: -38px;
            width: 30px;
            height: 30px;
            background-color: #464646;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 5px;
            opacity: 0;
            transition: 0.4s;
            &:hover{
                opacity: 1;
                cursor: pointer;
            }
            img{
                width: 17px;
            }
        }
        .reverseArrow{
            img{
                width: 17px;
                transform: rotateY(180deg);
            }
        }
        .containerOfLeft{
            height: 100%;
            .aboutAndBigName{
                height: 50%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                .about{
                    width: 160px;
                    position: relative;
                    animation: fromLeftToRight 1.1s ;
                    left: 42%;
                    transform: translateX(-50%);
                    h3{
                        color: black;
                    }
                    p{
                        color: black;
                        font-weight: 500;
                        font-size: 0.7em;
                    }
                }
                .bigName{
                    display: flex;
                    z-index: 5;
                    justify-content: center;
                    position: relative;
                    top: 5px;
                    animation: fromLeftToRightTwo 1.1s;
                    left: 5px;
                    h1{
                        color: white;
                        font-size: 3.4em;
                        font-weight: 600;
                    }
                    @media screen and(max-width: 1186px){
                        font-size: 0.8em;
                    }
                }            
            }
            .berbarianPart{
                height: 50%;
                display: flex;
                flex-direction: column;
                justify-content: end;
                .berbarianFlag{
                    width: 160px;
                    position: relative;
                    animation: fromLeftToRight 1.1s;
                    left: 42%;
                    transform: translateX(-50%);
                    img{
                        width: 100px;
                        filter: blur(1.25px);
                        opacity: 0.9;
                    }
                }
            }
        }
        @media screen and(max-width: 970px) {
            width: 0%;
            position: relative;
    
            &:nth-child(2){
                animation: dispOfLeftElems 0.2s forwards;
              
            }
        }
    }
    .leftAnimRetrait{
        width: 0%;
        position: relative;

        &:nth-child(2){
            animation: dispOfLeftElems 0.2s forwards;
          
        }
    
    }
    >.delButtonAB{
        position: absolute;
        left: 10px;
        top: 13px;
        width: 30px;
        height: 30px;
        background: #464646;
        border-radius: 6px;
        transition: 0.2s;
        display: flex;
        align-items: center;
        justify-content: center;

        img{
            transform: rotateZ(180deg);
        }
        &:hover{
            cursor: pointer;
        }
    }
    .middlePart{
        background-color: #151515;
        width: 50%;
        padding: 11vh 0;
        transition: 0.2s;

       
        .containerOfMiddle{
            @media screen and(max-width: 970px) {
                position: relative;
            }
            display: flex;
            flex-direction: column;
            height: 100%;
            justify-content: space-between;
            .masterPieceZone{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;

        
                .masterPieceText{
                    animation: fromTopToBottom 1.1s;
                    h2{
                        color: white;
                        font-weight: 100;
                        letter-spacing: -1px;
                    }
                }
                .theMasterPiece{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: 33.43%;
                    height: 0;
                    background: #8d6c6c;
                    padding-top: 33.34%;
                    transform: translate(-50%, -50%);

                    @media screen and(max-width: 970px){
                        transform: translate(-50%, -50%) scale(2);
                    }
                    .containerOfMSTR{
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-direction: column;
                        .oneLineHeight{
                            display: flex;
                            height: 100%;
                            justify-content: space-between;

                            .oneLineWidth{
                                width: calc(100%);
                                height: 100%;
                                background-color: blue;
                            }
                        }
                    }
                    
                }
            }
            .socialZone{
                
                .myNetworks{
                    display: flex;
                    justify-content: space-between;
                    padding: 0;
                    width: 397px;
                    position: relative;
                    left: 50%;
                    transform: translateX(-50%);
                    top: 30px;
                    animation: fromBottomToTopTwo 1.1s;
                    @media screen and(max-width: 665px) {
                        width: 210px;
                        font-size: 0.7em;
                    }
                }
            }
        }
        @media screen and(max-width: 970px) {
            background-color: #151515;
            width: 62.5%;
            padding: 11vh 0;
            transition: 0.2s;
        }
    }
    .middleAnimRetrait{
        width: 62.5%;

        .containerOfMiddle{
            position: relative;

            .masterPieceZone{
                .theMasterPiece{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    width: 51.43%;
                    height: 0;
                    background: #8d6c6c;
                    padding-top: 51.34%;
                    transform: translate(-50%, -50%);
                }
            }
        }
    }
    .rightPart{
        transition: 0.2s;
        background-color: #1c1c1c;
        width: 25%;
        padding-top: 45px;

       
        .containerOfRight{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 100%;
            .topPart{
                width: 40%;
                position: relative;
                left: 40%;
                transform: translateX(-50%);

                .presentaionPart{
                    margin-bottom: 20px;
                    p{
                        font-size: 0.7em;
                        opacity: 0;
                        animation: fromLeftToRightTwo 1.1s 1.1s forwards;
                    }
                    h2{
                        font-size: 3em;
                        color: #fa3701;
                        margin-top: 16px;
                        opacity: 0;
                        animation: fromRightToLeft 1.1s 1.1s forwards;
                        @media screen and(max-width:691px) {
                            font-size: 1.7em;
                        }
                    }
                }
                .slideBars{
                    .boxSize{
                        margin-bottom: 21px;
                        .text{
                            width: 160%;
                            display: flex;
                            justify-content: space-between;
                        }
                        p{
                            font-size: 0.5em;
                            color: rgba(245, 245, 245, 0.3803921569);
                            font-weight: 400;
                            letter-spacing: 1px;
                            margin-bottom: 2px;
                        }
                        input[type=range] {
                            -webkit-appearance: none;  /* Override default CSS styles */
                            appearance: none;
                            width: 100%; /* Full-width */
                            height: 10px; /* Specified height */
                            background: #4a4646; /* Grey background */
                            outline: none; /* Remove outline */
                            opacity: 0.9; /* Set transparency (for mouse-over effects on hover) */
                            -webkit-transition: .2s; /* 0.2 seconds transition on hover */
                            transition: opacity .2s;
                            border-radius: 10px;
                            width: 160%;
                          }
                          
                          /* Mouse-over effects */
                          input[type=range]:hover {
                            opacity: 1; /* Fully shown on mouse-over */
                          }
                          
                          /* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
                          input[type=range]::-webkit-slider-thumb {
                            -webkit-appearance: none; /* Override default look */
                            appearance: none;
                            width: 25px; /* Set a specific slider handle width */
                            height: 25px; /* Slider handle height */
                            background: #645d5d; /* Green background */
                            cursor: pointer; /* Cursor on hover */
                            border-radius: 50%;
                            transition: 0.2s;
                          }
                          input[type=range]:hover::-webkit-slider-thumb {
                            background: #151515; 
                          }
                          .colors{
                            max-height: 18vh;
                            overflow-y: auto;
                            overflow-x: hidden;
                            width: 187%;

                            .colorsZone{
                                display: grid;
                                grid-template-columns: repeat(5, 1fr);
                                grid-column-gap: 0px;
                                grid-row-gap: 0px;
                            }
                            .addColorButton button{
                                outline: none;
                                border: none;
                                border-radius: 5px;
                                padding: 1px 11px;
                                color: white;
                            }
                            .addColorButton button:nth-child(2){
                                background: #fa3701;
                            }
                            .addColorButton button:nth-child(3){
                                background-color: #2a92b5;
                                margin-left: 5px;
                            }
                            &::-webkit-scrollbar {
                                width: 1px;
                            }
                              
                              /* Track */
                            &::-webkit-scrollbar-track {
                                background: #1c1c1c;
                            }
                              
                              /* Handle */
                            &::-webkit-scrollbar-thumb {
                                background: #888;
                            }
                              
                              /* Handle on hover */
                            &::-webkit-scrollbar-thumb:hover {
                                background: #555;
                            }
                          }
                    }
                }
            }
            .bottomPart{
                width: 30%;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                bottom: 120px;
                display: flex;
                justify-content: center;

                button{
                    background: #fa3701;
                    color: white;
                    border: 0;
                    outline: none;
                    padding: 8px 24px;
                    border-radius: 27px;
                    font-size: 1.25em;
                    transition: 0.2s;

                    @media screen and(max-width:665px) {
                        font-size: 0.85em;
                    }
                    &:hover{
                        cursor: pointer;
                        opacity: 0.8;
                    }
                }
            }
        }
        @media screen and(max-width: 970px){
            transition: 0.2s;
            background-color: #1c1c1c;
            width: 37.5%;
            padding-top: 45px;
        }
    }
    .rightAnimRetrait{
        width: 37.5%;
     
    }

    .absoluteElem{
        z-index: 4;
        .circle{
            width: 33%;
            position: absolute;
            border-radius: 50%;
            height: 0;
            padding-top: 33%;
            border: 2px solid rgba(128, 128, 128, 0.275);
            top: 50%;
            transform: translateY(-50%);
            left: 0;
        }
        .line{
            width: 98%;
            height: 2px;
            background-color: rgba(128, 128, 128, 0.275);
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }
        @media screen and(max-width: 970px) {
            display: none;
        }
    }
}
@keyframes fromRightToLeft {
    0%{
        transform: translateX(100px);
        opacity: 0;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes fromLeftToRight {
    0%{
        transform: translateX(-70%);
        opacity: 0;
    }
    100%{
        transform: translateX(-50%);
        opacity: 1;
    }
}
@keyframes fromLeftToRightTwo {
    0%{
        transform: translateX(-100px);
        opacity: 0;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes fromTopToBottom {
    0%{
        opacity: 0;
        transform: translateY(-25px);
    }
    50%{
        opacity: 0;
        transform: translateY(-25px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}
@keyframes fromBottomToTop {
    0%{
        opacity: 0;
        transform: translateY(25px);
    }
    50%{
        opacity: 0;
        transform: translateY(25px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}
@keyframes fromBottomToTopTwo {
    0%{
        opacity: 0;
        transform: translate(-50%,25px);
    }
    50%{
        opacity: 0;
        transform: translate(-50%,25px);
    }
    100%{
        opacity: 1;
        transform: translate(-50%,0);
    }
}
@keyframes dispOfLeftElems {
    0%{
        opacity: 1;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}